import React, { useEffect, useState } from "react";
import StateList from "./StateList";
import "@ionic/react/css/core.css";
import "./home.css";
import {
  IonTitle,
  IonRow,
  IonApp,
  IonHeader,
  IonToolbar,
  IonContent,
  IonGrid,
  IonCol,
  IonCardContent,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardSubtitle,
  IonSpinner,
  IonFooter,
} from "@ionic/react";
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

function Home() {
  const [newData, setNewData] = useState([]);
  const [worldData, setWorldData] = useState([]);
  const [country, setCountry] = useState([]);
  const [loading, setLoading] = useState(false);

  const CovidData = async () => {
    try {
      const res1 = await fetch("https://covid-19-tracking.p.rapidapi.com/v1", {
        method: "GET",
        headers: {
          "x-rapidapi-key":
            "463ab71e14mshf3f55ff793aa776p10ea82jsn0a7e8a79e836",
          "x-rapidapi-host": "covid-19-tracking.p.rapidapi.com",
        },
      });
      const data1 = await res1.json();
      setCountry(data1);
      setNewData(data1[2]);
      setWorldData(data1[0]);
      setLoading(true);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    CovidData();
  }, []);

  return (
    <div className="home">
      {loading ? (
        <>
          <IonApp>
            <IonHeader>
              <IonToolbar color="dark">
                <IonTitle>
                  <h1>
                    <b>Covid-19 INDIA</b>
                  </h1>
                </IonTitle>
              </IonToolbar>
            </IonHeader>
            <IonContent color="dark" className="ion-padding">
              <IonGrid>
                <IonRow>
                  {/* Card-1 starts here*/}
                  <IonCol>
                    <IonCard color="warning" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {newData["Total Cases_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>
                          ({newData["New Cases_text"]})
                        </IonCardSubtitle>
                      </IonCardHeader>

                      <IonCardContent className="content">
                        Total Cases
                      </IonCardContent>
                    </IonCard>
                  </IonCol>
                  {/* Card-1 end here */}
                  <IonCol>
                    <IonCard color="danger" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {newData["Total Deaths_text"]}
                        </IonCardTitle>{" "}
                        <IonCardSubtitle>
                          ({newData["New Deaths_text"]})
                        </IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Deaths</IonCardContent>
                    </IonCard>
                  </IonCol>
                  <IonCol>
                    <IonCard color="primary" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {newData["Active Cases_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>-</IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Active</IonCardContent>
                    </IonCard>
                  </IonCol>
                  <IonCol>
                    <IonCard color="success" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {newData["Total Recovered_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>-</IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Recovered</IonCardContent>
                    </IonCard>
                  </IonCol>
                </IonRow>
                {/* world data */}
                <IonRow>
                  <IonToolbar color="dark">
                    <IonTitle size="large">World Covid Highlights</IonTitle>
                  </IonToolbar>
                </IonRow>

                <IonRow>
                  {/* Card-1 starts here*/}
                  <IonCol>
                    <IonCard color="warning" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {worldData["Total Cases_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>
                          ({worldData["New Cases_text"]})
                        </IonCardSubtitle>
                      </IonCardHeader>

                      <IonCardContent className="content">
                        Total Cases
                      </IonCardContent>
                    </IonCard>
                  </IonCol>

                  <IonCol>
                    <IonCard color="danger" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {worldData["Total Deaths_text"]}
                        </IonCardTitle>{" "}
                        <IonCardSubtitle>
                          ({worldData["New Deaths_text"]})
                        </IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Deaths</IonCardContent>
                    </IonCard>
                  </IonCol>
                  <IonCol>
                    <IonCard color="primary" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {worldData["Active Cases_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>-</IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Active</IonCardContent>
                    </IonCard>
                  </IonCol>
                  <IonCol>
                    <IonCard color="success" className="card">
                      <IonCardHeader>
                        <IonCardTitle>
                          {worldData["Total Recovered_text"]}
                        </IonCardTitle>
                        <IonCardSubtitle>-</IonCardSubtitle>
                      </IonCardHeader>
                      <IonCardContent>Recovered</IonCardContent>
                    </IonCard>
                  </IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>
                    <p>Last updated: {newData["Last Update"]}</p>
                  </IonCol>
                </IonRow>
              </IonGrid>
              <StateList />
              <>
                <IonHeader className="ion-padding">
                  <IonToolbar color="dark">
                    <IonTitle size="large">
                      Top 20 Covid affected Countries
                    </IonTitle>
                  </IonToolbar>
                </IonHeader>
                <IonRow className="ion-padding">
                  <IonCol>Country</IonCol>
                  <IonCol>Total Cases</IonCol>
                  <IonCol>Deaths</IonCol>
                  <IonCol>Active Cases</IonCol>
                  <IonCol>Recovered</IonCol>
                </IonRow>
                <IonCol>
                  {country.map((current, index) => {
                    if (index !== 0 && index < 21) {
                      return (
                        <IonRow className="ion" key={index} onClick={() => {}}>
                          <IonCol className="ion-padding cell-class">
                            {current["Country_text"]}
                          </IonCol>
                          <IonCol className="cell-class">
                            {current["Total Cases_text"]}
                          </IonCol>
                          <IonCol className="cell-class lg">
                            {current["Total Deaths_text"]}
                          </IonCol>
                          <IonCol className="cell-class">
                            {current["Active Cases_text"]}
                          </IonCol>
                          <IonCol className="cell-class">
                            {current["Total Recovered_text"]}
                          </IonCol>
                        </IonRow>
                      );
                    }
                  })}
                </IonCol>
              </>
              <p>Thanks for visiting my site - Vedant Chak</p>
            </IonContent>
          </IonApp>
        </>
      ) : (
        <IonSpinner />
      )}
    </div>
  );
}

export default Home;
